.footer {
	// margin-top: rem(145);

	display: flex;
	// flex-wrap: wrap;
	justify-content: space-between;
	padding-top: rem(70);
	padding-bottom: rem(65);

	&__left {
		margin-right: rem(154);
		max-width: rem(370);
		.logo {
			width: rem(167);
			display: block;
			margin-bottom: rem(32);
		}

		.text {
			font-size: rem(12);
		}
	}

	&__right {
		display: flex;
		flex-wrap: wrap;
		// flex-grow: 1;
		justify-content: space-between;

		> div {
			width: calc(100% / 2);
		}

		.icon_block {
			margin-bottom: rem(35);
			&:nth-child(2) {
				justify-content: flex-end;
			}
		}

		&-item {
			font-size: rem(12);
			&:last-child {
				justify-content: flex-end;
				text-align: right;
			}
		}
	}
}