.header {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	margin-top: rem(26);
	margin-bottom: rem(33);

	&__left {
		margin-right: rem(29);
	}

	&__center {

		nav {

			ul {

				display: flex;
				flex-wrap: wrap;

				li {
					margin-right: rem(26);

					&:last-child {
						margin-right: 0;
					}
					a {

						line-height: rem(24);
						text-transform: uppercase;
						transition: all 300ms ease;

						&:hover {
							color: var(--c-yellow);
						}
					}
				}
			}
		}
	}

	&__right {
		display: flex;
		flex-wrap: wrap;
		margin-right: rem(20);

		.icon_block {
			margin-right: rem(37);

			&:last-of-type {
				margin-right: 0;
			}
		}
	}
}